var data={buddhism:{seoTitle:'Contexts and Dialogue: Yog\u0101c\u0101ra Buddhism and Modern Psychology on the Subliminal Mind, by Tao Jiang',seoDescription:'Contexts and Dialogue: Yog\u0101c\u0101ra Buddhism and Modern Psychology on the Subliminal Mind is a fascinating study of the subliminal mind across cultural boundaries. It proposes a new paradigm in comparative approach to ideas through contextualization and recontextualization.',title:'Contexts and Dialogue: Yog\u0101c\u0101ra Buddhism and Modern Psychology on the Subliminal Mind',publisher:'University of Hawaii Press',bookLink:'http://a.co/d/1nYnDJI',themeColor:'rgb(236, 133, 19)',bookIntroduction:['Are there Buddhist conceptions of the unconscious? If so, are they more Freudian, Jungian, or something else? If not, can Buddhist conceptions be reconciled with the Freudian, Jungian, or other models? These are some of the questions that have motivated modern scholarship to approach *\u0101layavij\xF1\u0101na*, the storehouse consciousness, formulated in Yog\u0101c\u0101ra Buddhism as a subliminal reservoir of tendencies, habits, and future possibilities.','Tao Jiang argues convincingly that such questions are inherently problematic because they frame their interpretations of the Buddhist notion largely in terms of responses to modern psychology. He proposes that, if we are to understand *\u0101layavij\xF1\u0101na* properly and compare it with the unconscious responsibly, we need to change the way the questions are posed so that *\u0101layavij\xF1\u0101na* and the unconscious can first be understood within their own contexts and then recontextualized within a dialogical setting. In so doing, certain paradigmatic assumptions embedded in the original frameworks of Buddhist and modern psychological theories are exposed. Jiang brings together Xuan Zang\u2019s *\u0101layavij\xF1\u0101na* and Freud\u2019s and Jung\u2019s unconscious to focus on what the differences are in the thematic concerns of the three theories, why such differences exist in terms of their objectives, and how their methods of theorization contribute to these differences.','Contexts and Dialogue puts forth a fascinating, erudite, and carefully argued presentation of the subliminal mind. It proposes a new paradigm in comparative philosophy that examines the what, why, and how in navigating the similarities and differences of philosophical systems through contextualization and recontextualization.'],primaryReview:{review:'\u201CThe wonderful sense of conclusion that Jiang brings to the reader is conveyed in the idea that there is much to be learned through interdisciplinary discussion. Jiang emphasizes that this discussion cannot take place at a shallow level, as very little new knowledge can be gained thereby. The point is made that we must learn to engage with different disciplines through what he calls \u2018\u2018context and dialogue.\u2019\u2019 Overall, Jiang\u2019s main argument for the importance of creating a new methodology for cross-disciplinary research in this way is presented very successfully.\u201D',reviewer:'Philosophy East & West',magazine:true},otherReviews:[{review:'\u201CJiang\u2019s book succeeds on three levels: first, it offers an exceptionally clear introduction to Yog\u0101c\u0101ra Buddhism; second, it illuminates one very general and important difference between the models of mind in Buddhism and western psychology; and finally, it provides the right methodological cue for scholars engaged in comparative philosophical work. One leaves this book convinced that synthetic dialogue between Buddhism and modern psychology is possible, but that it will falter without adequate analysis of the paradigmatic differences that shape their respective projects.\u201D',reviewer:'H-Net Reviews',magazine:false},{review:'\u201CJiang\u2019s work \u2026 is a cautious reminder about the potential dangers of comparison that is helpful in the current academic climate, where comparative approaches are so popular. All who undertake comparative endeavors (even those who are not interested in Yoga\u0304ca\u0304ra Buddhism) should read Jiang\u2019s book.\u201D',reviewer:'Buddhist-Christian Studies',magazine:true},{review:'\u201CContexts and Dialogue rewards readers with useful and yet succinct introductions to the work of three seminal thinkers, and convincingly expands the methodological horizons of comparative philosophy.\u201D',reviewer:'Journal of Chinese Philosophy',magazine:true},{review:'\u201CJiang\u2019s stimulating work will appeal to a wide audience. He has succeeded in balancing the needs of various readers along with methodological issues and in so doing stayed focused on his primary goals. Overall, the work is a great success, points to new possibilities for dialogue between these viewpoints specifically, and attempts at cross-cultural study more generally.\u201D',reviewer:'Dao',magazine:true}]},freud:{seoTitle:'The Reception and Rendition of Freud in China',seoDescription:'The Reception and Rendition of Freud in China explores the history, reception, and use of Freud and his theories in China, and makes an original and substantial contribution to our understanding of the Chinese people and their culture as well as to our appreciation of western attempts to understand the people and culture of China.',title:'The Reception and Rendition of Freud in China: China\u2019s Freudian Slip',publisher:'Routledge',bookLink:'http://a.co/d/g77CGA7',// bookCoverColor: 'rgb(11, 81, 120)',
themeColor:'#DC3023',bookIntroduction:['Although Freud makes only occasional, brief references to China and Chinese culture in his works, for almost a hundred years many leading Chinese intellectuals have studied and appropriated various Freudian theories. However, whilst some features of Freud\u2019s views have been warmly embraced from the start and appreciated for their various explanatory and therapeutic values, other aspects have been vigorously criticized as implausible or inapplicable to the Chinese context.','This book explores the history, reception, and use of Freud and his theories in China, and makes an original and substantial contribution to our understanding of the Chinese people and their culture as well as to our appreciation of western attempts to understand the people and culture of China. The essays are organised around three key areas of research. First, it examines the historical background concerning the China-Freud connection in the 20th century, before going on to use reconstructed Freudian theories in order to provide a modernist critique of Chinese culture. Finally, the book deploys traditional Chinese thought in order to challenge various aspects of the Freudian project. Both Freudianism\u2019s universal appeal and its cultural particularity are in full display throughout the book. At the same time, the allure of Chinese cultural and literary expressions, both in terms of their commonality with other cultures and their distinctive characteristics, are also scrutinized.','This collection of essays will be welcomed by those interested in early modern and contemporary China, as well as the work and influence of Freud. It will also be of great interest to students and scholars of psychology, psychoanalysis, literature, philosophy, religion, and cultural studies more generally.']},papers:{seoTitle:'Some papers by Tao Jiang',seoDescription:'Select papers by Tao Jiang on Buddhist philosophy, Chinese philosophy and comparative philosophy.',categories:['Chinese Philosophy','Buddhist Philosophy','Comparative Philosophy'],shortCategories:['Chinese Phil.','Buddhist Phil.','Comparative Phil.'],themeColor:'rgb(0, 216, 208)',items:[{title:'\u201COneness and its Discontent: Contesting Ren in Classical Chinese Philosophy,\u201D in *The Oneness Hypothesis: Beyond the Boundary of Self*, edited by Philip J. Ivanhoe, Owen Flanagan, Victoria Harrison, Hagop Sarkissian, and Eric Schwitzgebel, Columbia University Press, 2018',tags:0,pathname:'Contesting_Ren_in_Classical_Chinese_Philosophy.pdf'},{title:'\u201CThe Problem of Authorship and the Project of Chinese Philosophy: ZHUANG Zhou and the Zhuangzi between Sinology and Philosophy in Western Academy,\u201D *Dao: A Journal of Comparative Philosophy* 15.1, 2016',tags:0,pathname:'Problem_of_Authorship.pdf'},{title:'\u201CIncommensurability of Two Conceptions of Reality: Dependent Origination and Emptiness in N\u0101g\u0101rjuna\u2019s MMK,\u201D *Philosophy East and West* 64.1, 2014',tags:1,pathname:'Incommensurability_of_Two_Conceptions_of_Reality.pdf'},{title:'\u201CIsaiah Berlin\u2019s Challenge to the Zhuangzian Freedom,\u201D *Journal of Chinese Philosophy* 2012',tags:[0,2],pathname:'Zhuangzi_and_Berlin_on_freedom_JCP.pdf'},{title:'\u201CLinji and William James on Mortality: Two Visions of Pragmatism,\u201D in *Mortality in Traditional China*, edited by Amy Olberding and Philip J. Ivanhoe. SUNY, 2011',tags:[0,1,2],pathname:'Linji_and_William_James_on_Mortality.pdf'},{title:'\u201CThe Dilemma of Skillful Means in Buddhist Pedagogy: Desire and Educations in the Lotus S\u016Btra,\u201D in *Educations and Their Purposes: A Philosophical Dialogue among Cultures*, edited by Roger Ames and Peter Hershock, University of Hawaii Press, 2007',tags:1,pathname:'Dilemma_of_Skillful_Means_in_Buddhist_Pedagogy.pdf'},{title:'\u201CIntimate Authority: The Rule of Ritual in Classical Confucian Political Discourse,\u201D Chapter Two in *Confucian Cultures of Authority in Practice: China*, edited by Roger Ames and Peter Hershock. State University of New York Press, 2006',tags:0,pathname:'Intimate_Authority.pdf'},{title:'\u201C*\u0100layavij\xF1\u0101na* and the Problematic of Continuity in the Cheng Weishi Lun,\u201D *Journal of Indian Philosophy* 33:3, 2005',tags:1,pathname:'Ālayavijñāna_and_continuity_JIP.pdf'},{title:'\u201CAccessibility of the Subliminal Mind: Transcendence vs. Immanence,\u201D *Continental Philosophy Review* 38:3-4, 2005',tags:[1,2],pathname:'Accessibility_of_the_subliminal_mind.pdf'},{title:'\u201CThe Problematic of Continuity: Nishida Kitar\u014D and Aristotle,\u201D *Philosophy East & West* 55:3, 2005',tags:2,pathname:'Nishida_and_Aristotle_on_continuity_PEW.pdf'},{title:'\u201CThe Storehouse Consciousness and the Unconscious: A Comparative Study of Xuan Zang and Freud on the Subliminal Mind,\u201D *Journal of the American Academy of Religion* 72:1, 2004',tags:[1,2],pathname:'Ālayavijñāna_and_unconscious_JAAR.pdf'},{title:'\u201CThe Role of History in Chan/Zen Enlightenment,\u201D *Dao: A Journal of Comparative Philosophy* IV.1, 2004',tags:[0,1],pathname:'Role_of_History_in_Chan_Zen_Enlightenment.pdf'},{title:'\u201CA Buddhist Scheme in Engaging Modern Science: The Case of Taixu (1890-1947),\u201D *Journal of Chinese Philosophy* 29:4, 2002',tags:[0,1],pathname:'Taixu_and_modern_science_JCP.pdf'},{title:'\u201CThe Problematic of Whole/Part and the Horizon of the Enlightened in Huayan Buddhism,\u201D *Journal of Chinese Philosophy* 28:4, 2001',tags:[0,1],pathname:'Whole-part_in_Huayan_JCP.pdf'}]},events:{seoTitle:'Tao Jiang\'s events',seoDescription:'Notable public events featuring Tao Jiang',academic:[{location:'Rutgers University, New Brunswick, NJ',subject:'Chinese Philosophy Project',introduction:'In order to promote a better understanding of Chinese philosophy, Tao Jiang launched the Rutgers Workshop on Chinese Philosophy (RWCP) in 2012 with Stephen Angle (Wesleyan) and Ruth Chang (Rutgers) (Dean Zimmerman took over from Ruth Chang as a co-director in 2018 when Change moves to Oxford). RWCP is designed to build a bridge between Chinese philosophy and Western analytic philosophy, promoting critical engagement and constructive dialogue between the two sides, with the hope of bringing the study of Chinese philosophy into the mainstream of philosophical discourse within the Western academy. It is run every other April. [Click here](https://rccs.rutgers.edu/news/159-fourth-rutgers-workshop-on-chinese-philosophy-rwcp#!IMG_1460) to the most recent meeting.'}],pub:[{location:'China Institute, New York, NY',subject:'Religion and the Rebuilding of China\'s Moral Foundations',introduction:'On June 7, 2018, Tao Jiang participated in [a panel discussion at the China Institute](https://rccs.rutgers.edu/news/166-tao-jiang-participated-in-a-panel-discussion-at-the-china-institute-in-nyc) on \u201CReligion and the Rebuilding of China\'s Moral Foundations,\u201D with freelance writer Ian Johnson (Pulitzer-prize winner and the author of *The Souls of China: The Return of Religion after Mao*), moderated by Los Angeles Times journalist Barbara Demick (former LA Times Beijing and Seoul bureau chief and the author of *Nothing to Envy: Ordinary Lives in North Korea*). The event received an extensive [coverage in the Sing Tao Daily \u661F\u5CF6\u65E5\u5831](https://www.singtaousa.com/ny/436-%E7%B4%90%E7%B4%84/792717-%E5%B8%8C%E6%9C%9B%E7%94%A8%E5%82%B3%E7%B5%B1%E5%83%B9%E5%80%BC%E8%A7%80%E9%87%8D%E5%BB%BA%E4%BF%A1%E4%BB%B0%E9%AB%94%E7%B3%BB+%E4%B8%AD%E5%9C%8B%E5%B0%8D%E5%AE%97%E6%95%99%E5%8C%85%E5%AE%B9%E5%BA%A6%E6%9C%89%E6%98%8E%E9%A1%AF%E6%8F%90%E9%AB%98/), a newspaper widely circulated in the Chinese community.',// videoLink: 'https://media.githubusercontent.com/media/biyunwu/academic-web/master/src/data/video/moral.mp4'
videoLink:'<iframe width="100%" height="100%" src="https://www.youtube-nocookie.com/embed/IefIdc4ms0o?rel=0&amp;showinfo=0" frameborder="0" allow="autoplay; encrypted-media" allowfullscreen></iframe>'}]},about:{seoTitle:'Tao Jiang\'s bio',seoDescription:'Tao Jiang\'s bio',name:'Tao Jiang',details:['Tao JIANG grew up in the ancient city of [Chengdu](https://www.nytimes.com/2016/03/06/t-magazine/chengdu-china-travel-guide.html) in China\u2019s southwest, \u201Cwhere the pace is slow, the food is spicy and there\u2019s always time for a good gossip over a cup of tea.\u201D When he was a boy, Jiang often dreamt of \u201Can adventurous life in a faraway place,\u201D which probably explains why he came all the way to America to pursue his dream. He is a scholar of classical Chinese philosophy, Mah\u0101y\u0101na Buddhist philosophy, and comparative philosophy. He is associate professor in the [Department of Religion](http://religion.rutgers.edu/tao-jiang) and an associate member of the graduate faculty in the Department of Philosophy at Rutgers, the State University of New Jersey in New Brunswick, US. He is director of the multidisciplinary [Rutgers Center for Chinese Studies](https://rccs.rutgers.edu).','Although America is far enough from China, an academic life devoted to philosophical inquiry does not sound like much of an adventure. Except it *is* an adventure: a genuine philosophical inquiry is an adventure of the mind in which that very inquisitive mind is its own limit. Jiang\u2019s approach to philosophy is to treat it as a way to study a particular form of intellectual, moral and material culture and the people who produce it.','Jiang is the author of *Contexts and Dialogue: Yog\u0101c\u0101ra Buddhism and Modern Psychology on the Subliminal Mind* and the co-editor of *The Reception and Rendition of Freud in China: China\u2019s Freudian Slip*. His articles have appeared in leading Asian and comparative philosophy journals and several major anthologies. Currently he is completing a monograph on classical Chinese philosophy. Jiang co-directs the Rutgers Workshop on Chinese Philosophy and co-chairs the Neo-Confucian Studies Seminar at Columbia University. He is currently serving in the program committee of the American Academy of Religion.','Jiang lives in the NYC metro area. He enjoys hanging out in coffee shops, museums, parks, and watching movies. He plays tennis from time to time and occasionally indulges in watching US Open Tennis Championship in person. He supports charities that are dedicated to saving the world\'s wildlife. These amazing videos [\u201CHungry polar bear surprises a seal\u201D] (https://www.youtube.com/watch?v=0mgnf6t9VEc), [\u201CElephant Calf Starves To Death Due To Drought\u201D] (https://www.youtube.com/watch?v=RqeC6XB94zs) from the [BBC Earth series] (https://www.facebook.com/bbcearth) have opened his eyes to the beauty, intelligence and misery of animal lives in the wilderness. Check them out, if you haven\'t, and your view about wildlife will never be the same again.']}};var getData=function getData(){return JSON.stringify(data);};

